export default {
  name: 'suba-mainnav',
  data () {
    return {
      mainitems: [],
      dropitems: [],
      isMainNavFetched: false,
      isOpen: false,
      isCollapse: true
    }
  },
  created () {
    this.fetchMainMenu()
  },
  methods: {
    fetchMainMenu () {
      this.$http.get('/list/mainmenu').then((response) => {
        var data = response.data

        if (data.success === true) {
          this.mainitems = data.msg.main || []
          this.dropitems = data.msg.drop || []
          this.isMainNavFetched = true
        }
      }, (error) => {
        throw error
      })
    },
    toggleDropdown () {
      if (this.isOpen === false) {
        this.isOpen = true
      } else {
        this.isOpen = false
      }
    },
    toggleCollapse () {
      if (this.isCollapse === false) {
        this.isCollapse = true
      } else {
        this.isCollapse = false
      }
    }
  }
}



