export default {
  name: 'suba-topnav',
  data () {
    return {
      topitems: [],
      isTopNavFetched: false
    }
  },
  created () {
    this.fetchTopMenu()
  },
  methods: {
    fetchTopMenu () {
      this.$http.get('/list/topmenu').then((response) => {
        var data = response.data

        if (data.success === true) {
          this.topitems = data.msg.top
          this.isTopNavFetched = true
        }
      }).catch((error) => {
        throw error
      })
    }
  },
  watch: {
    '$route': 'fetchTopMenu'
  }
}



