export default {
  name: 'suba-breadcrumb',
  props: {
    links: {
      type: Array,
      default: []
    },
    isShow: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    links (val, oldVal) {
      if (val.length > 0) {
        this.isShow = true
      } else {
        this.isShow = false
      }
    }
  }
}



