export default{
  name: 'suba-sidebar',
  data () {
    return {
      isShowContent: false
    }
  },
  methods: {
    showContent: function () {
      if (this.isShowContent === false) {
        this.isShowContent = true
      } else {
        this.isShowContent = false
      }
    }
  }
}



